import React, { useEffect, useState } from 'react';
import { generateNextSku } from '../../tables/tables';
import { supabase } from '../../supabase';
import Layout from '../../components/Layout';


export default function CreateItem() {
  const [items, setItems] = useState<any[]>([]);
  const [initialSkuNumber, setInitialSkuNumber] = useState<number | null>(null);
  const [saving, setSaving] = useState(false);
  const [message, setMessage] = useState('');

  useEffect(() => {
    const init = async () => {
      const baseSku = await generateNextSku(0);
      if (!baseSku) return;

      const baseNumber = parseInt(baseSku.replace('SKU-', ''));
      setInitialSkuNumber(baseNumber);

      setItems([
        {
          item: '',
          sku: formatSku(baseNumber),
          bulk: 0,
          carton: 0,
          price_each: 0
        }
      ]);
    };

    init();
  }, []);

  const formatSku = (n: number) => `SKU-${n.toString().padStart(3, '0')}`;

  const handleChange = (index: number, field: string, value: string | number) => {
    const updated = [...items];
    updated[index][field] =
      field === 'item' || field === 'sku' ? value : parseFloat(value as string) || 0;
    setItems(updated);
  };

  const addRow = () => {
    if (initialSkuNumber === null) return;

    const nextIndex = items.length;
    const nextSku = formatSku(initialSkuNumber + nextIndex);

    setItems(prev => [
      ...prev,
      { item: '', sku: nextSku, bulk: 0, carton: 0, price_each: 0 }
    ]);
  };

  const deleteRow = (index: number) => {
    const updated = [...items];
    updated.splice(index, 1);
    setItems(updated);
  };

  const saveAllItems = async () => {
    setMessage('');
    let invalid = false;
    const zeroPriceRows: number[] = [];

    const validItems = items.filter((item, idx) => {
      const { item: name, sku, bulk, carton } = item;
      if (
        typeof name !== 'string' || name.trim() === '' ||
        typeof sku !== 'string' || sku.trim() === '' ||
        isNaN(bulk) || bulk <= 0 ||
        isNaN(carton) || carton <= 0
      ) {
        invalid = true;
        return false;
      }

      if (item.price_each === 0) {
        zeroPriceRows.push(idx);
      }

      return true;
    });

    if (invalid) {
      alert('‚ùå Please fill all required fields with valid values before saving.');
      return;
    }

    if (zeroPriceRows.length > 0) {
      const proceed = confirm(`‚ö†Ô∏è ${zeroPriceRows.length} item(s) have $0 price. Continue anyway?`);
      if (!proceed) return;
    }

    const payload = validItems.map(({ item, sku, bulk, carton, price_each }) => ({
      name: item,
      sku,
      bulk,
      carton,
      price_each
    }));

    setSaving(true);

    try {
      const { error } = await supabase.from('price_items').insert(payload);

      if (error) {
        console.error('Insert error:', error.message);
        setMessage(`‚ùå Error: ${error.message}`);
        return;
      }

      // Remove inserted items from list
      const remaining = items.filter(item => !validItems.includes(item));
      setItems(remaining);
      setMessage(`‚úÖ ${payload.length} item(s) added successfully!`);
    } catch (err: any) {
      console.error('Unexpected error:', err);
      setMessage('‚ùå Unexpected error occurred.');
    } finally {
      setSaving(false);
    }
  };

  return (
    <Layout>
    <div style={{ padding: 20, fontFamily: 'Arial' }}>
      <h2>Create Items</h2>

      {items.length === 0 ? (
        <p>Loading...</p>
      ) : (
        <>
          <table border={1} cellPadding={6} cellSpacing={0}>
            <thead>
              <tr>
                <th>Item</th>
                <th>SKU</th>
                <th>Bulk</th>
                <th>Carton</th>
                <th>Cost Ea.</th>
                <th>Price Ea.</th>
                <th>Profit</th>
                <th>Profit Margin</th>
                <th>‚ùå</th>
              </tr>
            </thead>
            <tbody>
              {items.map((row, idx) => {
                const costEa = row.carton > 0 ? row.bulk / row.carton : 0;
                const profit = row.price_each - costEa;
                const margin = costEa > 0 ? (profit / costEa) * 100 : 0;

                return (
                  <tr key={idx}>
                    <td>
                      <input
                        value={row.item}
                        onChange={e => handleChange(idx, 'item', e.target.value)}
                      />
                    </td>
                    <td>
                      <input
                        value={row.sku}
                        onChange={e => handleChange(idx, 'sku', e.target.value)}
                      />
                    </td>
                    <td>
                      <input
                        type="number"
                        value={row.bulk}
                        onChange={e => handleChange(idx, 'bulk', e.target.value)}
                      />
                    </td>
                    <td>
                      <input
                        type="number"
                        value={row.carton}
                        onChange={e => handleChange(idx, 'carton', e.target.value)}
                      />
                    </td>
                    <td>${costEa.toFixed(2)}</td>
                    <td>
                      <input
                        type="number"
                        value={row.price_each}
                        onChange={e => handleChange(idx, 'price_each', e.target.value)}
                      />
                    </td>
                    <td>${profit.toFixed(2)}</td>
                    <td>{margin.toFixed(1)}%</td>
                    <td>
                      <button onClick={() => deleteRow(idx)}>‚ùå</button>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>

          <div style={{ marginTop: 16 }}>
            <button onClick={addRow}>‚ûï Add Row</button>{' '}
            <button onClick={saveAllItems} disabled={saving}>
              {saving ? '‚è≥ Saving...' : 'üíæ Save All'}
            </button>
          </div>
        </>
      )}

      {message && <p style={{ marginTop: 12 }}>{message}</p>}
    </div>
    </Layout>
  );
}
