import { supabase } from '../supabase';

export async function fetchPriceItems() {
  const { data, error } = await supabase
    .from('price_items')
    .select('uid, name, sku, bulk, carton');

  if (error) {
    console.error('Error fetching price items:', error.message);
    return [];
  }

  return data.map(item => ({
    uid: item.uid,
    item: item.name,
    sku: item.sku,
    bulk: parseFloat(item.bulk),
    carton: parseInt(item.carton),
    qty: 1,
    sell: 0
  }));
}



export async function fetchPriceItemById(id: string) {
  const { data, error } = await supabase
    .from('price_items')
    .select('*')
    .eq('uid', id)
    .single();

  if (error) {
    console.error('Fetch item error:', error.message);
    return null;
  }

  return data;
}

export async function savePriceItem(uid: string, updates: any) {
  const { error } = await supabase
    .from('price_items')
    .update(updates)
    .eq('uid', uid);

  return { success: !error, error };
}

export async function generateNextSku() {
  const { count, error } = await supabase
    .from('price_items')
    .select('uid', { count: 'exact', head: true });

  if (error) {
    console.error('Error counting rows in price_items:', error.message);
    return null;
  }

  const nextNumber = (count ?? 0) + 1;

  const skuNumber = nextNumber.toString().padStart(3, '0'); // ensures 3 digits
  return `SKU-${skuNumber}`;
}

export async function fetchSupplyLists(): Promise<string[]> {
  const { data, error } = await supabase
    .from('supply_list_meta')
    .select('list_name')
    .order('list_name', { ascending: true });

  if (error) {
    console.error('Error fetching list names from meta:', error.message);
    return [];
  }

  return data.map(row => row.list_name);
}



export async function fetchSupplyListDetails(listName: string) {
  const { data, error } = await supabase
    .from('supply_list')
    .select(`
      quantity,
      sell,
      item:price_items (
        uid,
        name,
        sku,
        bulk,
        carton
      )
    `)
    .eq('list_name', listName);

  if (error) {
    console.error('Error fetching supply list details:', error.message);
    return { items: [], meta: null };
  }

  const items = (data || []).map(row => {
    const costEa = parseFloat(row.item.bulk) / row.item.carton;
    const sell = parseFloat(row.sell ?? 0);
    const qty = row.quantity ?? 1;
    const profit = (sell - costEa) * qty;

    return {
      uid: row.item.uid,
      item: row.item.name,
      sku: row.item.sku,
      bulk: parseFloat(row.item.bulk),
      carton: row.item.carton,
      costEa,
      qty,                 // ‚úÖ Important: use 'qty' not 'quantity'
      sell,
      profit
    };
  });

  const { data: metaData, error: metaError } = await supabase
    .from('supply_list_meta')
    .select('margin, shipping_rate')
    .eq('list_name', listName)
    .limit(1)
    .single();

  if (metaError) {
    console.error('Error fetching supply list meta:', metaError.message);
  }

  return {
    items,
    meta: metaData || null
  };
}


export async function saveSupplyListItems(listName: string, items: any[], upsert = false) {
  // Convert to valid rows with required fields
  const rows = (items || [])
    .filter(i => i.uid && typeof i.qty === 'number' && typeof i.sell === 'number')
    .map(i => ({
      list_name: listName,
      item_uid: i.uid, // ‚úÖ match schema field
      quantity: i.qty > 0 ? i.qty : 1,
      sell: isNaN(i.sell) ? 0 : parseFloat(i.sell)
    }));

  if (rows.length === 0) {
    console.warn('‚ö†Ô∏è No valid items to upsert.');
    return false;
  }

  const { error } = upsert
    ? await supabase
        .from('supply_list')
        .upsert(rows, { onConflict: ['list_name', 'item_uid'] })
    : await supabase
        .from('supply_list')
        .insert(rows);

  if (error) {
    console.error(`‚ùå Error ${upsert ? 'upserting' : 'inserting'} supply list items:`, error.message);
    return false;
  }

  return true;
}





export async function editSupplyListItems(listName: string, items: any[], overwrite = true) {
  if (!items.length) {
    console.warn('‚ö†Ô∏è No valid items to upsert.');
    return false;
  }

  const { error } = await supabase
    .from('supply_list')
    .upsert(items, { onConflict: ['list_name', 'item_uid'] });

  if (error) {
    console.error('‚ùå Supabase upsert error:', error.message);
    return false;
  }

  return true;
}


export async function saveSupplyListMeta(listName: string, margin: number, shipping: number) {
  const { error } = await supabase
    .from('supply_list_meta')
    .upsert(
      [{ list_name: listName, margin, shipping_rate: shipping }],
      { onConflict: ['list_name'] }
    );

  if (error) {
    console.error('‚ùå Supabase error saving supply_list_meta:', error.message);
    return false;
  }

  return true;
}

export async function checkListNameExists(listName: string): Promise<boolean> {
  const { data, error } = await supabase
    .from('supply_list_meta')
    .select('list_name')
    .eq('list_name', listName)
    .limit(1);

  if (error) {
    console.error('Error checking list name:', error.message);
    return false;
  }

  return !!(data && data.length > 0);
}

export async function fetchSupplyListMeta(listName: string) {
  const { data, error } = await supabase
    .from('supply_list_meta')
    .select('margin, shipping_rate')
    .eq('list_name', listName)
    .limit(1)
    .single();

  if (error) {
    console.error('Error fetching supply list meta:', error.message);
    return null;
  }

  return data;
}

export async function deleteSupplyList(listName: string) {
  const { error: itemError } = await supabase
    .from('supply_list')
    .delete()
    .eq('list_name', listName);

  const { error: metaError } = await supabase
    .from('supply_list_meta')
    .delete()
    .eq('list_name', listName);

  if (itemError || metaError) {
    console.error('‚ùå Failed to delete list:', itemError?.message || metaError?.message);
    return false;
  }

  return true;
}
